const api = require('../../config/api.js')
const util = require('../../utils/util.js')
const user = require("../../services/user.js")
const app = getApp()	
export default {
	data() {
		return {
			order: {},
			userInfo: {}
			
		}
	},
	//初始化
	onLoad() {

	},
	// 页面显示
	onShow() {
		this.getInfo();
	},
	//分享
	onShareAppMessage() {
		wx.showShareMenu({
			withShareTicket:true
		})
		let code = wx.getStorageSync('code');
		this.setCode(code)
		return {
			title: '@你：这里每天更新好多免费的抽奖，快来抽奖吧~',
			path: '/pages/user/index?code='+code,
			imageUrl: '../../static/images/shareImage.jpg'
		}
	},
	//下拉刷新
	onPullDownRefresh(e){
		setTimeout(function () {
			wx.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		//获取个人中心信息
		getInfo() {
			util.request(api.getUserInfo,{},'get').then(res=>{
				if(res.code == 0) {
					this.userInfo = res.data.user_id;
					this.order = res.data.order;
					this.getShareTicket()
				}
			})
		},
		//跳转到充值页面
		rechargePage() {
			wx.navigateTo({
				url: '/pages/pay/recharge'
			})
		},
		//获取分享数据得到的群信息
		getShareTicket() {
			let ticket = wx.getStorageSync('ticket');
			let pages = getCurrentPages();
			let { options } = pages.pop();
			var that = this;
			if(ticket && options.code != "") {
				wx.getShareInfo({
					shareTicket: ticket,
					success:function(res) {
						let params = {
							code: options.code ? options.code : "",
							iv: res.iv,
							data: res.encryptedData
						}	
						console.log(params)
						that.getShareInfos(params)
					}
				})
			}
		},
		
		//新增分享记录
		getShareInfos(params) {
			util.request(api.setShareRechge, params,'post').then(res=>{
				console.log(res)
			})
		},
		//传递分享code进去
		setCode(code) {
			util.request(api.setShareInfo,{code: code},'post').then(res=>{})
		}
	}
}
